/* --------------------------------------------------------------------------

    Structure

   -------------------------------------------------------------------------- */
.l-container {
  display: block;
  &::after {
    position: fixed;
    content: "";
    top: 0;
    left: 0;
    z-index: 99999;
    width: 100%;
    height: 100%;
    pointer-events: none;
    background-color:$whiteColor;
    opacity: 0;
  }
  .is-active + &::after {
    animation: fadeOutLoading 1.5s 5.5s ease forwards;
  }
}

@include tab {
  .l-contents-inner {
    margin: 0 auto;
    max-width: $contentsWidth;
  }
}
